header {
    display: block;
}

.masthead {
    position: relative;
    z-index: -1;
    top: -65px;
    /* height: auto; */
    width: 100%;
    height: 60%;
    background: no-repeat center center;
    background-color: #777;
    background-attachment: scroll;
    background-size: cover;
}

.Welcome-title {
    font-family: 'Leckerli One', cursive;
    color: white;
}

.WelcomeHeader {
    height: 100%;
    background-color: rgba(79,98,114,0.4);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}


